import { Link, PathRouteProps } from "react-router-dom";
import { Trans } from "react-i18next";

interface ILinkButton {
  label?: string;
  target?: string;
}

interface LinkProps extends ILinkButton, PathRouteProps {}
function LinkButton(props: LinkProps) {
  const { path, label, target } = props;
  return (
    <Link
      to={path as string}
      className="text-primary-primaryBlue hover:text-primary-buttonPrimaryHoverColor text-xs focus:text-secondary-gray-500"
      target={target}
    >
      <Trans>{label}</Trans>
    </Link>
  );
}

export default LinkButton;
